.x-axis path,
.x-axis line,
.y-axis path,
.y-axis line {
  shape-rendering: crispEdges;
}

.x-axis text,
.y-axis text {
  font-family: sans-serif;
  font-size: 13px;
}

g.arc {
  fill: steelblue;
}


g.label text {
  text-anchor: middle;
  font-family: sans-serif;
  font-size: 13px;
  fill: #666;
}

.App {
  display: flex;
  height: 100vh;
  justify-content: space-evenly;
}

.stations,
.stations svg {
  position: absolute
}


.stations svg {
  width: 60px;
  height: 20px;
  padding-right: 100px;
  font: 10px sans-serif;
}

.stations circle {
  fill: brown;
  stroke: black;
  stroke-width: 1.5px;
}

.legends-container {
  padding: 10px;
  border: black 1px solid;
  padding-top: 15px;
  margin-bottom: 20px;
  display: flex;
  width: 40%;
  justify-content: space-evenly;
}

.legend-container {

  display: flex;
  align-items: center;
  margin-bottom: 5px;
}